.element__like-button {
    /*align-self: flex-end;
    margin-right: 22px;
    margin-left: auto;*/
    background-color: transparent;
    background-image: url(../../../images/heart-image.svg);
    background-size: contain;
    background-repeat: no-repeat;
    outline: none;
    width: 20px;
    height: 18px;
    border: none;
}

.element__like-button:hover {
    opacity: 0.6;
    cursor: pointer;
    transition: opacity .5s linear 0s;
}